"use strict";
window.chrome.runtime.onInstalled.addListener(() => {
    window.chrome.storage.sync.get(['oldDesign'], (result) => {
        if (result.oldDesign !== undefined) {
            window.chrome.storage.local.set({ oldDesign: result.oldDesign });
        }
    });
});
window.chrome.storage.onChanged.addListener((changes, areaName) => {
    if (areaName === 'sync' && changes.oldDesign) {
        window.chrome.storage.local.set({ oldDesign: changes.oldDesign.newValue });
    }
});
